import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			mobileNo: '18428396534',
			verificationCode: '',
			password: '',
			rePassword: '',
			submitting: false,
			verification: true, // 通过v-show控制显示获取还是倒计时
			timer: 5, // 定义初始时间为60s
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		//发送短信验证码
		sendCode() {
			const {
				mobileNo,
				password,
				verificationCode,
				rePassword
			} = this;
			if (!this.$api.util.validateMobileNo(mobileNo)) {
				this.$api.msg('手机号码格式错误');
				return
			}
			this.$api.request.sms({
				type: 'MOBILE',
				mobileCountryCode: '86',
				mobileNo: mobileNo
			}, loginRes => {
				if (loginRes.body.status.statusCode === '0') {
					this.$api.msg('验证码已发送');
					this.timer = 5
					this.verification = false
					this.countDown() // 执行验证码计时
				} else {
					this.$api.msg(loginRes.body.status.errorDesc);
				}
			});
		},
		// 验证码倒计时
		countDown() {
			let auth_timer = setInterval(() => {
				// 定时器设置每秒递减
				this.timer-- // 递减时间
				uni.setStorage({
					key: 'timer',
					data: this.timer,
					success: function() {}
				})
				if (this.timer <= 0) {
					this.verification = true // 60s时间结束还原v-show状态并清除定时器
					clearInterval(auth_timer)
				}
			}, 1000)
		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		async toForgetPassword() {
			this.submitting = true;

			const {
				mobileNo,
				verificationCode,
				password,
				rePassword
			} = this;
			var isFormValid = true;
			if (!this.$api.util.validateMobileNo(mobileNo)) {
				this.$api.msg('手机号码格式错误');
				isFormValid = false;
			}
			if (!this.$api.util.validateVerificationCode(verificationCode)) {
				this.$api.msg('请输入6位数字的验证码');
				isFormValid = false;
			} else if (!this.$api.util.validatePassword(password)) {
				this.$api.msg('密码为8-20位字母数字下划线组合');
				isFormValid = false;
			} else if (password != rePassword) {
				this.$api.msg('两次密码输入不一致');
				isFormValid = false;
			}
			if (!isFormValid) {
				this.submitting = false;
				return;
			}
			const requestData = {
				type: 'MOBILE',
				mobileNo: mobileNo,
				mobileCountryCode: '86',
				verificationCode: verificationCode
			};
			this.$api.request.verifyCode(requestData, verifyRes => {
				if (verifyRes.body.status.statusCode === '0') {
					//验证码校验成功后重置密码
					this.$api.request.resetPassword({
						verifyType: 'PASSWORD',
						verificationCode: verificationCode,
						personalPhone: mobileNo,
						personalPhoneCountryCode: '86',
						newPassword: password
					}, resetRes => {
						if (resetRes.body.status.statusCode === '0') {
							this.$api.msg('密码已重置');
							setTimeout(function() {
								wx.navigateTo({
									url: '/pages/public/login'
								})
							}, 2000)
						} else {
							this.$api.msg(resetRes.body.status.errorDesc);
							this.submitting = false;
						}
					});
				} else {
					this.$api.msg(verifyRes.body.status.errorDesc);
					this.submitting = false;
				}
			});
			// const result = await this.$api.json('userInfo');
			// if (result.status === 1) {
			// 	this.login(result.data);
			// 	uni.navigateBack();
			// } else {
			// 	this.$api.msg(result.msg);
			// 	this.submitting = false;
			// }
		}
	},

}
